@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);

* {
  margin: 0;
  padding: 0;
  font-family: 'Bebas Neue', cursive;
  font-weight: 600;
  letter-spacing: 0.1em;
}
p,
span {
  font-family: 'Poppins', sans-serif;
  letter-spacing: 0;
}

body {
  /* font-family: 'Montserrat', sans-serif; */
  overflow-x: hidden;
  background: white;
}

button:focus,
button:active {
  outline: none;
}

.footer {
  bottom: 0;
  position: relative;
  z-index: 999;
  background-color: #292929;
}
@media (min-width: 700px) {
  .body_content_padding_top {
    padding-top: 80px;
  }
}
@media (max-width: 700px) {
  .body_content_padding_top {
    padding-top: 120px;
  }
}

.pagination {
  display: flex;
}

.pagination_items {
  padding: 10px 20px;
  color: black;
  font-weight: 700;
  border: 2px solid black;
  margin: 5px;
  border-radius: 5px;
}

.swiper-container {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  display: none;
}

